import "./App.css";
import { useState, useEffect } from "react";
import { getCode } from "./Blackbox";
import axios from 'axios';
import CodeEditor from '@uiw/react-textarea-code-editor';

const langs_txt = require("./langs.txt");

export default function App(): any {
  const [response, setResponse] = useState('');
  const [langs, setLangs] = useState([]);
  const [input, setInput] = useState('');
  const [code, setCode] = useState('');
  const [output, setOutput] = useState('');
  const [thinking, setThinking] = useState('');

  useEffect(() => {
    axios.get(langs_txt).then(response => setLangs(response.data.split("\n")));
  }, []);

  const InputLanguageSelect = () => {
    return (
      <select value={input} onChange={(element) => setInput(element.target.value)}>
        {langs.map((lang, key) => (
          <option key={key}>{lang}</option>
        ))}
      </select>
    );
  };

  const OutputLanguageSelect = () => {
    return (
      <select value={output} onChange={(element) => setOutput(element.target.value)}>
        {langs.map((lang, key) => (
          <option key={key}>{lang}</option>
        ))}
      </select>
    );
  };

  const submit = () => {
    setThinking("thinking...");
    getCode(output, input, code).then((result) => {
      setResponse(result);
      setThinking('');
    });
  }

  return (
    <div id="main">
      <center>
        <div id="header">
          <h1>AI Code Translator</h1>
          <p>Translate your code to other language using Blackbox AI</p>
        </div>
      </center>
      <div id="editors">
        <div id="input">
          <p>
            Input
            <InputLanguageSelect />
          </p>
          <CodeEditor onChange={(element) => setCode(element.target.value)} 
              style={{fontSize: 16}} padding={15} className="code-editor" 
              language={"javascript"} />
        </div>
        <div id="output">
          <p>
            Output
            <OutputLanguageSelect />
          </p>
          <CodeEditor value={response} readOnly={false} style={{fontSize: 16}} padding={15} 
              className="code-editor" language={"javascript"} />
        </div>
      </div>
      <center>
        <div id="translate">
          <button onClick={submit}>Translate</button>
          <p>{thinking}</p>
        </div>
        <div id="footer">
          <p className="footer-p">
            made by
            <a href="https://github.com/strokegmd"> strokegmd </a>
            //<a href="https://github.com/strokegmd/strokegmd.github.io"> view on github</a></p>
        </div>
      </center>
    </div>
  );
}
